@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #d2dae2;
  font-family: 'Quicksand', sans-serif; }

p {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 12pt;
  color: #fff;
  line-height: 20pt; }

strong {
  font-weight: 700; }

.ui.progress .bar {
  background: #1B9CFC; }

.ui.button {
  background-color: #c8d6e5; }

.ui.active.button {
  background: #1B9CFC;
  color: #fff; }

.ui.button:hover, .ui.active.button:hover {
  background: #AFDDFF;
  border-left: 2px; }

.ui.buttons .or::before {
  color: #8395a7; }

.ui.table {
  border: 0;
  background-color: inherit; }

.ui.celled.table tr td {
  background-color: #fff; }

.header {
  background-color: #182C61; }
  .header .right {
    float: right; }
  .header h1 {
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    color: #fff;
    font-size: 12pt;
    padding: 15px 0;
    margin: 0; }
  .header a {
    margin: 0;
    padding: 0;
    color: #fff;
    text-transform: uppercase;
    padding-top: 16px;
    font-weight: 500;
    font-size: 10pt;
    padding-bottom: 1px;
    border-bottom: 1px solid #182C61;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  .header a:hover {
    border-bottom: 1px solid #fff; }

.calculatorForm {
  background-color: #fff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 30px 50px; }
  .calculatorForm p {
    color: #000;
    padding-bottom: 15px; }
  .calculatorForm .question {
    border: 1px solid #c8d6e5;
    border-radius: 5px;
    padding: 30px 50px;
    margin-bottom: 30px; }
    .calculatorForm .question img {
      padding-bottom: 20px; }
    .calculatorForm .question select {
      border: 1px solid #1B9CFC; }
    .calculatorForm .question input[type="radio"] {
      display: none; }
    .calculatorForm .question input[type="radio"] + label p {
      font-size: 10pt;
      line-height: 14pt;
      text-align: left;
      border-left: 1px solid #c8d6e5;
      border-right: 1px solid #c8d6e5;
      margin: 0;
      padding: 0px 31px;
      cursor: pointer; }
    .calculatorForm .question input[type="radio"] + label p:first-child {
      border-top: 1px solid #c8d6e5;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      padding-top: 11px;
      margin-top: 1px; }
    .calculatorForm .question input[type="radio"] + label p:last-child {
      border-bottom: 1px solid #c8d6e5;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      padding-bottom: 11px;
      margin-bottom: 11px; }
    .calculatorForm .question input[type="radio"] + label .points {
      float: right;
      opacity: 0.6;
      font-weight: 600;
      position: relative; }
    .calculatorForm .question input[type="radio"] + label:hover p {
      border-left: 2px solid #1B9CFC;
      border-right: 2px solid #1B9CFC;
      padding-left: 30px;
      padding-right: 30px; }
    .calculatorForm .question input[type="radio"] + label:hover p:first-child {
      border-top: 2px solid #1B9CFC;
      padding-top: 10px; }
    .calculatorForm .question input[type="radio"] + label:hover p:last-child {
      border-bottom: 2px solid #1B9CFC;
      padding-bottom: 10px; }
    .calculatorForm .question input[type="radio"]:checked + label p {
      border-left: 2px solid #1B9CFC;
      border-right: 2px solid #1B9CFC;
      background-color: #1B9CFC;
      color: #fff;
      padding-left: 30px;
      padding-right: 30px; }
    .calculatorForm .question input[type="radio"]:checked + label p:first-child {
      border-top: 2px solid #1B9CFC;
      padding-top: 10px; }
    .calculatorForm .question input[type="radio"]:checked + label p:last-child {
      border-bottom: 2px solid #1B9CFC;
      padding-bottom: 10px; }
  .calculatorForm .formBtn {
    background-color: #c8d6e5;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  .calculatorForm .formBtn:hover {
    background-color: #1B9CFC;
    color: #fff; }

.calculatorForm.success {
  border-top: 5px solid #1dd1a1;
  padding: 15px 40px; }
  .calculatorForm.success p {
    text-align: left;
    padding: 0; }
  .calculatorForm.success .formBtn {
    float: right;
    position: relative;
    top: -31px; }

.userData {
  background-color: #182C61;
  border-radius: 10px;
  margin-top: 30px;
  margin-bottom: 10px;
  padding: 20px 10px; }

.userDataSummary {
  background-color: #341f97;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 10px;
  padding: 20px 10px; }

.tableSort {
  background-color: #182C61;
  border-radius: 10px;
  margin-top: 0px;
  margin-bottom: 30px;
  padding: 10px 10px; }
  .tableSort p {
    font-weight: 600;
    display: inline-block;
    margin-right: 20px; }

.ui.table td:last-child.Likely {
  background-color: #1dd1a1;
  color: #097457; }

.ui.table td:last-child.Unlikely {
  background-color: #ff6b6b;
  color: #9E1313; }

.ui.table td:last-child.Average {
  background-color: #ffc061;
  color: #6e3601; }

.ui.table thead tr th {
  background-color: #222f3e;
  color: #fff;
  padding: 15px 20px;
  font-size: 12pt; }

.ui.table thead tr:first-child > th:first-child {
  border-top-left-radius: 10px; }

.ui.table thead tr:first-child > th:last-child {
  border-top-right-radius: 10px;
  text-align: center; }

.ui.table td {
  padding: 15px 20px;
  font-weight: 500;
  font-size: 12pt; }

.ui.table td:first-child {
  font-weight: 600; }

.ui.table td:nth-child(3) .Unlikely, .ui.table td:first-child .Unlikely, .ui.table td:nth-child(2) .Unlikely {
  color: #ff6b6b;
  background-color: #fff; }

.ui.table td:nth-child(3) .Likely, .ui.table td:first-child .Likely, .ui.table td:nth-child(2) .Likely {
  color: #1dd1a1;
  background-color: #fff; }

.ui.table td:nth-child(3) .Average, .ui.table td:first-child .Average, .ui.table td:nth-child(2) .Average {
  background-color: #fff;
  color: #000; }

.ui.table td:nth-child(3) .Unlikely span, .ui.table td:nth-child(3) .Likely span, .ui.table td:first-child .Unlikely span, .ui.table td:first-child .Likely span, .ui.table td:nth-child(2) .Unlikely span, .ui.table td:nth-child(2) .Likely span {
  font-size: 9pt;
  position: relative;
  top: -5px;
  left: 5px;
  font-weight: 600; }

.ui.table td:nth-child(3) .Likely span::before, .ui.table td:first-child .Likely span::before, .ui.table td:nth-child(2) .Likely span::before {
  content: "+"; }

.ui.table td:nth-child(3) .Average span, .ui.table td:first-child .Average span, .ui.table td:nth-child(2) .Average span {
  display: none; }

.ui.table td:last-child {
  text-align: center;
  text-transform: uppercase;
  font-size: 11pt;
  font-weight: 600; }

.messly {
  text-transform: none !important;
  border-top: 2px solid #df3e3b !important; }
  .messly p {
    color: #000;
    font-size: 10pt;
    font-weight: 600;
    cursor: pointer; }
  .messly strong {
    color: #df3e3b; }
  .messly .start {
    background-color: #df3e3b;
    color: #fff;
    padding: 7px 10px;
    margin-left: 20px;
    border-radius: 5px;
    border: 2px solid #fff; }
  .messly :hover .start {
    color: #df3e3b;
    background-color: #fff;
    border: 2px solid #df3e3b; }

.disclaimer {
  margin: 50px 0; }
  .disclaimer p {
    color: #000;
    font-size: 10pt;
    font-weight: 600; }
  .disclaimer .notice {
    background-color: #8395a7;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: 600; }
    .disclaimer .notice a {
      color: #000;
      text-decoration: underline; }
  .disclaimer .build {
    background-color: #c8d6e5;
    width: 250px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px; }
    .disclaimer .build p {
      color: #8395a7; }
      .disclaimer .build p a {
        color: #8395a7;
        font-weight: 700; }
      .disclaimer .build p a:hover {
        color: #1B9CFC; }

.footer {
  text-align: center;
  padding: 30px;
  padding-bottom: 60px;
  margin-top: -30px; }
  .footer button {
    background: #8395a7 !important;
    color: #d2dae2 !important; }
  .footer button:hover {
    background: #ff6b6b !important;
    color: #fff !important; }

@media only screen and (max-width: 767px) {
  .ui.table thead {
    display: none !important; }
  .ui.table tr td:first-child {
    text-align: center;
    padding-bottom: 10px; }
  .ui.table tr td:nth-child(3) {
    text-align: center; }
  .ui.table tr .Unlikely span, .ui.table tr .Likely span {
    display: none; }
  .ui.table tr td:nth-child(2) {
    display: none !important; }
  input[type="radio"] + label p {
    padding: 10px 15px !important; }
  .points {
    display: none; }
  .calculatorForm.success .formBtn {
    text-align: center;
    float: none;
    top: 5px; }
  .start {
    display: block; }
  .ui.table:not(.unstackable) tr {
    box-shadow: none;
    padding-top: 0;
    padding-bottom: 10px; } }

